import React from "react";
import { Link } from "react-router-dom";

const Movie = props => (
  <div
    className="card"
    style={{
      display: "inline-block",
      width: "248px",
      margin: "10px 10px",
      verticalAlign: "top"
    }}
  >
    <div
      className="card-image waves-effect waves-block waves-light"
      style={{ borderBottom: "1px solid #ccc" }}
    >
      <img
        alt={props.movie.title}
        className="activator"
        src={props.movie.poster}
        style={{
          height: "200px",
          width: "auto",
          margin: "0 auto"
        }}
      />
    </div>
    <div className="card-content">
      <span className="card-title activator grey-text text-darken-4">
        {props.movie.title}
        <i className="material-icons right">more_vert</i>
      </span>
      <p>
        {localStorage.getItem("loggedInUser") ? (
          <div>
            {props.movie.movieuser_id === 0 ? (
              <i
                className="material-icons right"
                style={{
                  color: "#26a69a",
                  cursor: "pointer"
                }}
                onClick={event => props.shownMovie(event, props.movie.movie_id)}
              >
                remove_red_eye
              </i>
            ) : (
              <i
                className="material-icons right"
                style={{
                  color: "#26a69a"
                }}
              >
                beenhere
              </i>
            )}
          </div>
        ) : (
          <div>
            <i
              className="material-icons right"
              style={{
                color: "#ccc"
              }}
            >
              remove_red_eye
            </i>
          </div>
        )}
        <Link to={"/Comment/" + props.movie.movie_id}>View Comment</Link>
      </p>
    </div>
    <div className="card-reveal">
      <span className="card-title grey-text text-darken-4">
        {props.movie.title}
        <i className="material-icons right">close</i>
      </span>
      <p>Genres: {props.movie.genres}</p>
      <p>Release Year: {props.movie.release_year}</p>
      <p>Produced By: {props.movie.produced_by}</p>
      <p>Total Cost: {props.movie.cost}</p>
      <Link to={"/UpdateMovie/" + props.movie.movie_id}>Update Content</Link>
    </div>
  </div>
);

export default Movie;
