import React, { Component } from "react";
import Aux from "../../hoc/Aux";
import { Redirect } from "react-router-dom";

import Input from "../../components/UI/Input/Input";
import Button from "../../components/UI/Button/Button";

class MovieUpdate extends Component {
  state = {
    loading: false,
    redirect: false,
    movieForm: {
      title: "",
      poster: "",
      genres: "",
      release_year: "",
      produced_by: "",
      cost: "",
      participants: "",
      description: ""
    },
    movie: [
      {
        id: "title",
        type: "text",
        label: "Movie Title",
        icon: "title",
        outerClass: "row",
        innerClass: "input-field col s12"
      },
      {
        id: "poster",
        type: "text",
        label: "Movie Poster Url",
        icon: "photo",
        outerClass: "row",
        innerClass: "input-field col s12"
      },
      {
        id: "genres",
        type: "text",
        label: "Geners",
        icon: "photo",
        outerClass: "row",
        innerClass: "input-field col s12"
      },
      {
        id: "release_year",
        type: "text",
        label: "Released Year",
        icon: "date_range",
        outerClass: "row",
        innerClass: "input-field col s12"
      },
      {
        id: "produced_by",
        type: "text",
        label: "Produced By",
        icon: "camera_roll",
        outerClass: "row",
        innerClass: "input-field col s12"
      },
      {
        id: "cost",
        type: "text",
        label: "Total Cost",
        icon: "attach_money",
        outerClass: "row",
        innerClass: "input-field col s12"
      },
      {
        id: "participants",
        type: "text",
        label: "Actors and Actress",
        icon: "group",
        outerClass: "row",
        innerClass: "input-field col s12"
      },
      {
        id: "description",
        type: "textarea",
        label: "Summery",
        icon: "group",
        outerClass: "row",
        innerClass: "input-field col s12"
      }
    ]
  };

  componentDidMount() {
    this.setState({ loading: true });
    console.log(this.props.match.params.id);
    fetch(
      "http://localhost:8080/api/getMovieById/" + this.props.match.params.id
    )
      .then(response => response.json())
      .then(data => {
        this.setState({ loading: false });
        this.setState({ movieForm: data });
      })
      .catch(error => console.log(error));
  }

  UpdateMovieInfo = event => {
    event.preventDefault();

    const updatedMovieForm = { ...this.state.movieForm };

    fetch(
      "http://localhost:8080/api/updateMovie/" + this.props.match.params.id,
      {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*"
        },
        body: JSON.stringify(updatedMovieForm)
      }
    )
      .then(response => {
        response.text().then(data => {
          if (data === "true") {
            this.setState({ redirect: true });
          }
        });
        console.log(response);
      })
      .catch(errors => {
        console.log(errors);
      });
  };

  inputChangedHandler = (event, id) => {
    const updatedMovieForm = { ...this.state.movieForm };
    updatedMovieForm[id] = event.target.value;
    this.setState({ movieForm: updatedMovieForm });
  };

  render() {
    return (
      <Aux>
        {this.state.loading ? (
          <div>Loading...</div>
        ) : (
          <div>
            {this.state.redirect ? (
              <Redirect to="/" />
            ) : (
              <div className="row">
                <form className="col s6" onSubmit={this.UpdateMovieInfo}>
                  {this.state.movie.map(input => {
                    return (
                      <Input
                        key={input.id}
                        id={input.id}
                        type={input.type}
                        label={input.label}
                        icon={input.icon}
                        outerclass={input.outerClass}
                        innerclass={input.innerClass}
                        valuechange={event =>
                          this.inputChangedHandler(event, input.id)
                        }
                      />
                    );
                  })}
                  <Button label="Update Movie" />
                </form>
              </div>
            )}
          </div>
        )}
      </Aux>
    );
  }
}

export default MovieUpdate;
