import React from "react";

const CommentedMovie = props => {
  return (
    <div className="col s12 m7">
      <h2 className="header">{props.movie.title}</h2>
      <div className="card horizontal">
        <div className="card-image">
          <img alt={props.movie.title} src={props.movie.poster} />
        </div>
        <div className="card-stacked">
          <div className="card-content">
            <p>{props.movie.description}</p>
            <p>{props.movie.participants}</p>
          </div>
          <div className="card-action">
            Rate: {props.movie.rate} &nbsp; | &nbsp; Genres:{" "}
            {props.movie.genres} &nbsp; | &nbsp; Release Year:{" "}
            {props.movie.release_year} &nbsp; | &nbsp; Produced By:{" "}
            {props.movie.produced_by}
          </div>
        </div>
      </div>
    </div>
  );
};

export default CommentedMovie;
