import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";

class Header extends Component {
  state = {
    redirect: false,
    loggedInUser: ""
  };
  componentDidMount() {
    this.setState({ loggedInUser: localStorage.getItem("loggedInUser") });
  }
  logoutHandler = () => {
    localStorage.removeItem("loggedInUser");
    this.setState({ redirect: true });
  };
  render() {
    return (
      <nav>
        {this.state.redirect ? (
          <Redirect to="/" />
        ) : (
          <div className="nav-wrapper">
            <div style={{ width: "80%", margin: "0 auto" }}>
              <Link to="/" className="brand-logo">
                <i className="material-icons">cloud</i>Movie Store
              </Link>
              <ul id="nav-mobile" className="right hide-on-med-and-down">
                <li style={{ paddingRight: "15px" }}>
                  {this.state.loggedInUser}
                </li>
                <li>
                  <Link to="/">Home</Link>
                </li>
                <li>
                  <Link to="/MyMovies">My Movies</Link>
                </li>
                {this.state.loggedInUser ? (
                  <li>
                    <a onClick={this.logoutHandler}>Logout</a>
                  </li>
                ) : (
                  <li>
                    <Link to="/Authenticate">Login</Link>
                  </li>
                )}
              </ul>
            </div>
          </div>
        )}
      </nav>
    );
  }
}

export default Header;
