import React from "react";
import { Route, Switch } from "react-router-dom";

import Aux from "../../hoc/Aux";
import classes from "./Layout.module.css";
import Header from "../../container/Header/Header";

import Movies from "../../container/Movies/Movies";
import Authenticate from "../../container/Login/Login";
import Comment from "../../container/Comments/Comments";
import SignUp from "../../container/SignUp/SignUp";
import MovieRegistration from "../../container/MovieRegistration/MovieRegistration";
import MovieUpdate from "../../container/MovieRegistration/MovieUpdate";
import MyMovies from "../../container/MyMovies/MyMovies";

const Layout = props => (
  <Aux>
    <Header />
    <main className={classes.container}>
      <div className={classes.content}>
        <Switch>
          <Route path="/" exact component={Movies} />
          <Route path="/Authenticate" exact component={Authenticate} />
          <Route path="/SignUp" exact component={SignUp} />
          <Route path="/AddMovie" exact component={MovieRegistration} />
          <Route path="/MyMovies" exact component={MyMovies} />
          <Route path="/UpdateMovie/:id" exact component={MovieUpdate} />
          <Route path="/Comment/:id" props exact component={Comment} />
        </Switch>
      </div>
    </main>
  </Aux>
);

export default Layout;
