import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import Aux from "../../hoc/Aux";

import Input from "../../components/UI/Input/Input";
import Button from "../../components/UI/Button/Button";

class Login extends Component {
  state = {
    redirect: false,
    login: {
      email: "",
      password: ""
    },
    loginInfo: [
      {
        id: "email",
        type: "email",
        icon: "email",
        label: "Email",
        outerclass: "row",
        innerclass: "input-field col s12"
      },
      {
        id: "password",
        type: "password",
        icon: "lock",
        label: "Password",
        outerclass: "row",
        innerclass: "input-field col s12"
      }
    ]
  };
  inputChangedHandler = (event, id) => {
    let updatedLogin = { ...this.state.login };
    updatedLogin[id] = event.target.value;
    this.setState({ login: updatedLogin });
  };
  userAuthentication = event => {
    event.preventDefault();

    fetch("http://localhost:8080/api/validateUser", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "Access-Allow-Control-Origin": "*"
      },
      body: JSON.stringify(this.state.login)
    })
      .then(response => {
        response.text().then(data => {
          if (data === "true") {
            localStorage.setItem("loggedInUser", this.state.login.email);
            this.setState({ redirect: true });
          }
        });
      })
      .catch(error => {
        console.log(error);
      });
  };
  render() {
    return (
      <Aux>
        {this.state.redirect ? (
          <Redirect push={true} to="/" />
        ) : (
          <div>
            <div className="row">
              <form className="col s6" onSubmit={this.userAuthentication}>
                {this.state.loginInfo.map(input => {
                  return (
                    <Input
                      key={input.id}
                      id={input.id}
                      type={input.type}
                      icon={input.icon}
                      label={input.label}
                      outerclass={input.outerclass}
                      innerclass={input.innerclass}
                      valuechange={event =>
                        this.inputChangedHandler(event, input.id)
                      }
                    />
                  );
                })}
                <Button label="Login" />
              </form>
            </div>
            <div>
              Don't have an account? <Link to="/SignUp">Sign Up</Link>
            </div>
          </div>
        )}
      </Aux>
    );
  }
}

export default Login;
