import React from "react";

const Input = props => {
  let inputElement = null;

  switch (props.type) {
    case "textarea":
      inputElement = (
        <textarea
          className="materialize-textarea"
          {...props}
          onChange={props.valuechange}
        />
      );
      break;
    default:
      inputElement = (
        <input className="validate" {...props} onChange={props.valuechange} />
      );
  }

  return (
    <div className={props.outerclass}>
      <div className={props.innerclass}>
        <i className="material-icons prefix">{props.icon}</i>
        {inputElement}
        <label htmlFor={props.id}>{props.label}</label>
      </div>
    </div>
  );
};
export default Input;
