import React, { Component } from "react";
import Aux from "../../hoc/Aux";

import CommentForm from "./CommentForm";
import CommentedMovie from "./CommentedMovie";
import CommentsList from "./CommentsList";

class Comments extends Component {
  state = {
    loading: false,
    movie: [],
    comments: [],
    loggedInUser: "",
    comment: "",
    result: ""
  };

  componentDidMount() {
    this.setState({ loggedInUser: localStorage.getItem("loggedInUser") });
    this.setState({ loading: true });
    fetch("http://localhost:8080/api/comments/" + this.props.match.params.id)
      .then(result => result.json())
      .then(data => {
        this.setState({ movie: data[0] });
        this.setState({ comments: data[1] });
        this.setState({ loading: false });
      });
  }

  saveCommentHandler = event => {
    event.preventDefault();

    let updateComment = {
      movie_id: this.props.match.params.id,
      content: this.state.comment,
      given_by: localStorage.getItem("loggedInUser")
    };

    fetch("http://localhost:8080/api/addComment", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*"
      },
      body: JSON.stringify(updateComment)
    })
      .then(response => {
        response.text().then(data => {
          console.log(data);
          this.setState({ comments: data });
        });
      })
      .catch(error => {
        console.log(error);
      });
  };

  inputChangedHandler = event => {
    this.setState({ comment: event.target.value });
  };

  incrementLikeHandler = (event, comment_id) => {
    event.preventDefault();
    fetch(
      "http://localhost:8080/api/addLike/" +
        this.props.match.params.id +
        "/" +
        comment_id
    )
      .then(response => response.json())
      .then(data => this.setState({ comments: data }));
  };

  incrementDislikeHandler = (event, comment_id) => {
    event.preventDefault();
    fetch(
      "http://localhost:8080/api/addDislike/" +
        this.props.match.params.id +
        "/" +
        comment_id
    )
      .then(response => response.json())
      .then(data => this.setState({ comments: data }));
  };

  render() {
    return (
      <Aux>
        {this.state.loading ? (
          <div>Loading...</div>
        ) : (
          <div>
            <CommentedMovie movie={this.state.movie} />
            <CommentForm
              loggedInUser={this.state.loggedInUser}
              saveCommentHandler={this.saveCommentHandler}
              inputChangedHandler={this.inputChangedHandler}
            />
            {this.state.comments.map(data => {
              return (
                <CommentsList
                  incrementLikeHandler={this.incrementLikeHandler}
                  incrementDislikeHandler={this.incrementDislikeHandler}
                  loggedInUser={this.state.loggedInUser}
                  comment={data}
                />
              );
            })}
          </div>
        )}
      </Aux>
    );
  }
}

export default Comments;
