import React from "react";
import Aux from "../../hoc/Aux";
import Input from "../../components/UI/Input/Input";
import Button from "../../components/UI/Button/Button";

const CommentForm = props => {
  return (
    <Aux>
      <div className="row">
        {props.loggedInUser ? (
          <div>
            <form
              className="col s12"
              onSubmit={event => props.saveCommentHandler(event)}
            >
              <Input
                id="comment"
                type="textarea"
                icon="message"
                label="Comment"
                outerclass="row"
                innerclass="input-field col s12"
                valuechange={event => props.inputChangedHandler(event, this)}
              />
              <Button label="Add Comment" />
            </form>
          </div>
        ) : (
          <div>Login / Sign up to give your comment.</div>
        )}
      </div>
    </Aux>
  );
};

export default CommentForm;
