import React, { Component } from "react";
import Aux from "../../hoc/Aux";
import Movie from "../../components/Movie/Movie";
import { Link } from "react-router-dom";
import Input from "../../components/UI/Input/Input";
import { properties } from "../../Assets/properties";

class Movies extends Component {
  state = {
    beforFilter: true,
    movies: [],
    filteredMovies: [],
    loading: false,
    loggedInUser: localStorage.getItem("loggedInUser"),
    sampleMovies: properties.sampleMovies
  };

  componentDidMount() {
    this.setState({ loading: true });
    if (localStorage.getItem("loggedInUser")) {
      let loggedInUser = {
        email: localStorage.getItem("loggedInUser")
      };

      fetch("http://localhost:8080/api/getAllMoviesByLoggedInUser", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "Access-Allow-Control-Origin": "*"
        },
        body: JSON.stringify(loggedInUser)
      })
        .then(response => response.json())
        .then(data => {
          console.log(data);
          this.setState({ loading: false });
          this.setState({ movies: data });
        })
        .catch(error => {
          this.setState({ loading: false });
        });
    } else {
      fetch("http://localhost:8080/api/getAllMovies")
        .then(result => result.json())
        .then(data => {
          this.setState({ loading: false });
          this.setState({ movies: data });
        })
        .catch(error => {
          console.log(">>>>>> ", error);
          this.setState({ movies: this.state.sampleMovies });
          this.setState({ loading: false });
        });
    }
  }

  inputChangedHandler = (event, id) => {
    let searchFor = event.target.value;
    let filteredMovies = this.state.movies.filter(movie =>
      movie["title"].includes(searchFor)
    );
    if (this.state.filteredMovies.length > 0)
      this.setState({ beforFilter: false });
    this.setState({ filteredMovies: filteredMovies });
  };

  showenMovieMarkHandler = (event, movie_id) => {
    event.preventDefault();
    let loggedInUser = {
      email: localStorage.getItem("loggedInUser")
    };

    fetch("http://localhost:8080/api/watchedMovie/" + movie_id, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "Access-Allow-Control-Origin": "*"
      },
      body: JSON.stringify(loggedInUser)
    })
      .then(response => {
        response.text().then(data => this.setState({ movies: data }));
      })
      .catch(error => console.log(error));
  };

  render() {
    return (
      <Aux>
        <div class="row" style={{ borderBottom: "1px solid #ccc" }}>
          <div class="input-field col s8">
            {this.state.loggedInUser && (
              <Link
                to="/AddMovie"
                className="waves-effect waves-light btn-small"
                style={{ float: "left" }}
              >
                Add Movie
              </Link>
            )}
          </div>
          <Input
            id="search"
            type="text"
            label="Search"
            icon="search"
            outerclass=""
            innerclass="input-field col s4"
            style={{ float: "right" }}
            valuechange={event => this.inputChangedHandler(event, "search")}
          />
        </div>

        {this.state.loading ? (
          <div>Loading...</div>
        ) : (
          <div>
            {this.state.beforFilter ? (
              <div>
                {this.state.movies.map(movie => {
                  return (
                    <Movie
                      key={movie.movie_id}
                      movie={movie}
                      shownMovie={this.showenMovieMarkHandler}
                    />
                  );
                })}
              </div>
            ) : (
              <div>
                {this.state.filteredMovies.map(movie => {
                  return (
                    <Movie
                      key={movie.movie_id}
                      movie={movie}
                      shownMovie={this.showenMovieMarkHandler}
                    />
                  );
                })}
              </div>
            )}
          </div>
        )}
      </Aux>
    );
  }
}

export default Movies;
