import React from "react";
import classes from "./Comments.module.css";

const CommentsList = props => {
  return (
    <div className={classes.container}>
      <p>{props.comment.content}</p>
      <hr />
      <div>
        <div style={{ float: "left", position: "relative" }}>
          Given By: {props.comment.given_by}
        </div>
        {props.loggedInUser ? (
          <div style={{ float: "right" }}>
            <i
              className="material-icons prefix"
              style={{ cursor: "pointer", color: "#26a69a" }}
              onClick={event =>
                props.incrementLikeHandler(event, props.comment.comment_id)
              }
            >
              thumb_up
            </i>
            &nbsp;
            {props.comment.like_count} &nbsp;&nbsp;
            <i
              className="material-icons prefix"
              style={{ cursor: "pointer", color: "#26a69a" }}
              onClick={event =>
                props.incrementDislikeHandler(event, props.comment.comment_id)
              }
            >
              thumb_down
            </i>
            &nbsp;
            {props.comment.dislike_count}
          </div>
        ) : (
          <div style={{ float: "right" }}>
            <i className="material-icons prefix" style={{ color: "#ccc" }}>
              thumb_up
            </i>
            &nbsp;
            {props.comment.like_count} &nbsp;&nbsp;
            <i className="material-icons prefix" style={{ color: "#ccc" }}>
              thumb_down
            </i>
            &nbsp;
            {props.comment.dislike_count}
          </div>
        )}
      </div>
    </div>
  );
};

export default CommentsList;
