import React, { Component } from "react";
import Aux from "../../hoc/Aux";
import { Link } from "react-router-dom";

import Input from "../../components/UI/Input/Input";
import Button from "../../components/UI/Button/Button";

class SignUp extends Component {
  state = {
    userInfo: {
      first_name: "",
      last_name: "",
      email: "",
      password: "",
      confirmPassword: ""
    },
    user: [
      {
        id: "first_name",
        type: "text",
        label: "First Name",
        icon: "account_box",
        outerclass: "row",
        innerclass: "input-field col s12"
      },
      {
        id: "last_name",
        type: "text",
        label: "Last Name",
        icon: "account_box",
        outerclass: "row",
        innerclass: "input-field col s12"
      },
      {
        id: "email",
        type: "email",
        label: "Email",
        icon: "email",
        outerclass: "row",
        innerclass: "input-field col s12"
      },
      {
        id: "password",
        type: "password",
        label: "Password",
        icon: "lock",
        outerclass: "row",
        innerclass: "input-field col s12"
      },
      {
        id: "confirmPassword",
        type: "password",
        label: "Confirm Password",
        icon: "lock_outline",
        outerclass: "row",
        innerclass: "input-field col s12"
      }
    ]
  };
  inputChangedHandler = (event, id) => {
    let updatedUserInfo = { ...this.state.userInfo };
    updatedUserInfo[id] = event.target.value;
    this.setState({ userInfo: updatedUserInfo });
  };
  saveUserInformation = event => {
    event.preventDefault();
    let updatedUserInfo = {
      full_name:
        this.state.userInfo.first_name + " " + this.state.userInfo.last_name,
      email: this.state.userInfo.email,
      password: this.state.userInfo.password
    };
    console.log(JSON.stringify(updatedUserInfo));
    fetch("http://localhost:8080/api/addUser", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*"
      },
      body: JSON.stringify(updatedUserInfo)
    })
      .then(response => {
        response.text().then(data => console.log(data));
      })
      .catch(error => {
        console.log(error);
      });
  };
  render() {
    return (
      <Aux>
        <div className="row">
          <form className="col s6" onSubmit={this.saveUserInformation}>
            {this.state.user.map(user => {
              return (
                <Input
                  key={user.id}
                  id={user.id}
                  type={user.type}
                  label={user.label}
                  icon={user.icon}
                  outerclass={user.outerclass}
                  innerclass={user.innerclass}
                  valuechange={event =>
                    this.inputChangedHandler(event, user.id)
                  }
                />
              );
            })}
            <Button label="Sign Up" />
          </form>
        </div>
        <div>
          Do you have an account? <Link to="/Authenticate">Login</Link>
        </div>
      </Aux>
    );
  }
}

export default SignUp;
